.login-wrapper {
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  background-color: var(--white);
}
.login-wrapper .login-hero {
  background:
    linear-gradient(
      135deg,
      var(--primary) 0%,
      var(--primary-hover) 100%);
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--white);
  padding: 3rem;
}
.login-wrapper .login-hero::before {
  content: "";
  position: absolute;
  top: -10%;
  left: -10%;
  width: 50%;
  height: 50%;
  border-radius: 50%;
  background:
    radial-gradient(
      circle,
      rgba(255, 255, 255, 0.1) 0%,
      rgba(255, 255, 255, 0) 70%);
  z-index: 1;
}
.login-wrapper .login-hero::after {
  content: "";
  position: absolute;
  bottom: -10%;
  right: -10%;
  width: 60%;
  height: 60%;
  border-radius: 50%;
  background:
    radial-gradient(
      circle,
      rgba(255, 255, 255, 0.08) 0%,
      rgba(255, 255, 255, 0) 70%);
  z-index: 1;
}
.login-wrapper .login-hero .hero-content {
  position: relative;
  z-index: 2;
  text-align: center;
  max-width: 600px;
}
.login-wrapper .login-hero .hero-content h1 {
